"use client";
import React, { useState } from "react";
import Loader from "@/components/Loader";
import { useTranslation } from "react-i18next";

const Index = () => {
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");
  const [status, setStatus] = useState("");
  const [loading, setLoading] = useState(false); // Ğ¡Ğ¾ÑÑ‚Ğ¾ÑĞ½Ğ¸Ğµ Ğ·Ğ°Ğ³Ñ€ÑƒĞ·ĞºĞ¸
  const [formVisible, setFormVisible] = useState(false); // Ğ¡Ğ¾ÑÑ‚Ğ¾ÑĞ½Ğ¸Ğµ Ğ²Ğ¸Ğ´Ğ¸Ğ¼Ğ¾ÑÑ‚Ğ¸ Ñ„Ğ¾Ñ€Ğ¼Ñ‹
  const { t } = useTranslation();

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true); // ĞŸĞ¾ĞºĞ°Ğ·Ğ°Ñ‚ÑŒ Loader

    const formData = {
      email: email,
      message: message,
    };

    try {
      const response = await fetch(
        "https://bonusnumber1.com/api/sent_email_support.php",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(formData),
        }
      );

      setTimeout(() => {
        setLoading(false); // Ğ¡ĞºÑ€Ñ‹Ñ‚ÑŒ Loader Ñ‡ĞµÑ€ĞµĞ· 1 ÑĞµĞºÑƒĞ½Ğ´Ñƒ
      }, 1000);

      if (response.ok) {
        setStatus(t("The message was sent hastily!"));
        setTimeout(() => {
          setFormVisible(false); // Ğ—Ğ°ĞºÑ€Ñ‹Ğ²Ğ°ĞµĞ¼ Ñ„Ğ¾Ñ€Ğ¼Ñƒ Ñ‡ĞµÑ€ĞµĞ· 1 ÑĞµĞºÑƒĞ½Ğ´Ñƒ Ğ¿Ğ¾ÑĞ»Ğµ Ğ¾Ñ‚Ğ¿Ñ€Ğ°Ğ²ĞºĞ¸
        }, 1000);
      } else {
        setStatus(t("Error"));
      }
    } catch (error) {
      setStatus(t("Error"));
      setLoading(false); // Ğ¡ĞºÑ€Ñ‹Ñ‚ÑŒ Loader Ğ² ÑĞ»ÑƒÑ‡Ğ°Ğµ Ğ¾ÑˆĞ¸Ğ±ĞºĞ¸
    }
  };

  const toggleForm = () => {
    setFormVisible(!formVisible); // ĞŸĞµÑ€ĞµĞºĞ»ÑÑ‡Ğ¸Ñ‚ÑŒ Ğ²Ğ¸Ğ´Ğ¸Ğ¼Ğ¾ÑÑ‚ÑŒ Ñ„Ğ¾Ñ€Ğ¼Ñ‹
  };

  return (
    <div>
      <button onClick={toggleForm} className="btn btn-support">
        {t("Support")}
      </button>

      {formVisible && (
        <div className="form-support">
          {loading && (
            <div className="loader-overlay">
              <Loader /> {/* Ğ›Ğ¾Ğ°Ğ´ĞµÑ€ Ğ¿Ğ¾Ğ²ĞµÑ€Ñ… Ñ„Ğ¾Ñ€Ğ¼Ñ‹ */}
            </div>
          )}
          <div className={`formS ${loading ? "blurred" : ""}`}>
            <button onClick={toggleForm} className="close-btn">X</button> {/* ĞšĞ½Ğ¾Ğ¿ĞºĞ° Ğ´Ğ»Ñ Ğ·Ğ°ĞºÑ€Ñ‹Ñ‚Ğ¸Ñ Ñ„Ğ¾Ñ€Ğ¼Ñ‹ */}
            <h1>{t("Contact Us")}</h1>
            <form onSubmit={handleSubmit}>
              <div>
                <label>{t("Email")}:</label>
                <input
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required
                />
              </div>
              <div>
                <label>{t("Message")}:</label>
                <textarea
                  value={message}
                  onChange={(e) => setMessage(e.target.value)}
                  required
                />
              </div>
              <button className="btn btn-orange mt-4" type="submit">
                {t("Send")}
              </button>
            </form>
            {status && <p>{status}</p>}
          </div>
        </div>
      )}
    </div>
  );
};

export default Index;
