'use client';

import { useEffect, useState } from 'react';

export default function MainWrapper({ children }: { children: React.ReactNode }) {
  const [languageClass, setLanguageClass] = useState(() => {
    // Ğ˜Ğ½Ğ¸Ñ†Ğ¸Ğ°Ğ»Ğ¸Ğ·Ğ°Ñ†Ğ¸Ñ ÑĞ¾ÑÑ‚Ğ¾ÑĞ½Ğ¸Ñ Ğ¿Ñ€Ğ¸ Ğ¿ĞµÑ€Ğ²Ğ¾Ğ¹ Ğ·Ğ°Ğ³Ñ€ÑƒĞ·ĞºĞµ
    return typeof window !== 'undefined' ? localStorage.getItem('i18nextLng') || '' : '';
  });

  useEffect(() => {
    // ĞŸÑ€Ğ¾Ğ²ĞµÑ€ĞºĞ° Ğ½Ğ° Ğ½Ğ°Ğ»Ğ¸Ñ‡Ğ¸Ğµ Ğ¸Ğ·Ğ¼ĞµĞ½ĞµĞ½Ğ¸Ğ¹ Ğ² localStorage ĞºĞ°Ğ¶Ğ´Ñ‹Ğµ 500 Ğ¼Ñ
    const intervalId = setInterval(() => {
      const storedLanguage = localStorage.getItem('i18nextLng');
      if (storedLanguage !== languageClass) {
        setLanguageClass(storedLanguage || '');
      }
    }, 500); // ĞŸÑ€Ğ¾Ğ²ĞµÑ€ÑĞµĞ¼ ĞºĞ°Ğ¶Ğ´Ñ‹Ğµ Ğ¿Ğ¾Ğ»ÑĞµĞºÑƒĞ½Ğ´Ñ‹

    // ĞÑ‡Ğ¸ÑÑ‚ĞºĞ° Ğ¸Ğ½Ñ‚ĞµÑ€Ğ²Ğ°Ğ»Ğ° Ğ¿Ñ€Ğ¸ Ñ€Ğ°Ğ·Ğ¼Ğ¾Ğ½Ñ‚Ğ¸Ñ€Ğ¾Ğ²Ğ°Ğ½Ğ¸Ğ¸ ĞºĞ¾Ğ¼Ğ¿Ğ¾Ğ½ĞµĞ½Ñ‚Ğ°
    return () => {
      clearInterval(intervalId);
    };
  }, [languageClass]);

  return <main className={languageClass}>{children}</main>;
}
